/*fonts*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap");
@font-face {
  font-family: "Sapiens";
  src: url("../fonts/Sapiens-Regular.woff2") format("woff2"), url("../fonts/Sapiens-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Sapiens Bold";
  src: url("../fonts/Sapiens-Bold.woff2") format("woff2"), url("../fonts/Sapiens-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
h1,
.h1 {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1.0625rem;
}
@media (max-width: 767px) {
  h1,
  .h1 {
    font-size: 2.1875rem;
  }
}

h2,
.h2 {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}
@media (max-width: 767px) {
  h2,
  .h2 {
    font-size: 2.1875rem;
  }
}

h3,
.h3 {
  font-size: 2.375rem;
  line-height: 1.2;
  margin-bottom: 0.9375rem;
}
@media (max-width: 767px) {
  h3,
  .h3 {
    font-size: 1.875rem;
  }
}

h4,
.h4 {
  font-size: 1.5rem;
  line-height: 1.2;
  margin-bottom: 0.9375rem;
}
@media (max-width: 767px) {
  h4,
  .h4 {
    font-size: 1.25rem;
  }
}

h5,
.h5 {
  font-size: 1.3125rem;
  line-height: 1.43;
  margin-bottom: 0.9375rem;
}
@media (max-width: 767px) {
  h5,
  .h5 {
    font-size: 1.1875rem;
  }
}

h6,
.h6 {
  font-size: 1.25rem;
  line-height: 1.43;
  margin-bottom: 0.9375rem;
}
@media (max-width: 767px) {
  h6,
  .h6 {
    font-size: 1.125rem;
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  font-family: "Sapiens Bold";
  text-shadow: -2px 2px 0 #101D3F, 4px 4px 0 #101D3F, 2px -2px 0 #101D3F, -2px -2px 0 #101D3F;
}

p {
  line-height: normal;
  font-weight: 400;
  margin-bottom: 1.3125rem;
}

a:not(.btn):not(.dropdown-item):not(.nav-link) {
  color: #101D3F;
  text-decoration: none;
}
a:not(.btn):not(.dropdown-item):not(.nav-link):hover {
  text-decoration: underline;
}

strong,
b {
  font-weight: bold;
}

.text-white {
  color: white !important;
}
.text-white h1,
.text-white h2,
.text-white h3,
.text-white h4,
.text-white h5,
.text-white h6,
.text-white p,
.text-white a:not(.btn),
.text-white label,
.text-white .subtitle {
  color: inherit !important;
}

html {
  font-size: 16px;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  font-weight: 400;
  color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

.page-holder {
  padding-top: 0rem;
  overflow: hidden;
  position: relative;
}

section {
  width: 100%;
  float: left;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  padding: 4.375rem 0;
  z-index: 20;
}
section .container,
section .container-fluid {
  position: relative;
  z-index: 20;
}
@media (max-width: 990px) {
  section {
    padding: 3.125rem 0;
  }
}
@media (max-width: 767px) {
  section {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }
}

.navbar-toggler {
  position: relative;
  background-color: transparent;
  width: 3.125rem;
  min-width: 3.125rem;
  min-height: 3.25rem;
  border: none !important;
  transition: all 0.2s ease-in-out 0s;
}
@media (max-width: 990px) {
  .navbar-toggler {
    margin-right: 0.0625rem;
  }
}
@media (max-width: 767px) {
  .navbar-toggler {
    margin-top: -0.1875rem;
  }
}
@media (min-width: 767px) {
  .navbar-toggler {
    margin-right: -0.625rem;
  }
}
.navbar-toggler span {
  transition: all 0.2s ease-in-out 0s;
  background-color: #ffffff;
  height: 2px;
  position: absolute;
  width: 1.875rem;
  left: 50%;
  top: 50%;
  opacity: 1;
  margin-top: 0;
  border-radius: 0.5rem;
}
.navbar-toggler span:nth-of-type(1) {
  transform: translate(-50%, -0.625rem) rotate(0deg);
}
.navbar-toggler span:nth-of-type(2) {
  transform: translate(-50%, -50%) rotate(0deg);
}
.navbar-toggler span:nth-of-type(3) {
  transform: translate(-50%, 0.5rem) rotate(0deg);
}
.navbar-toggler:focus {
  text-decoration: none;
  outline: 0;
  box-shadow: none;
}
.navbar-toggler[aria-expanded=true] {
  color: transparent !important;
}
.navbar-toggler[aria-expanded=true] span:nth-of-type(1) {
  transform: translate(-50%, -50%) rotate(-45deg);
}
.navbar-toggler[aria-expanded=true] span:nth-of-type(2) {
  opacity: 0;
}
.navbar-toggler[aria-expanded=true] span:nth-of-type(3) {
  transform: translate(-50%, -50%) rotate(45deg);
}

.admin-bar .top-menu {
  top: 2.8125rem;
}
@media (min-width: 767px) {
  .admin-bar .top-menu {
    top: 2rem;
  }
}

.top-menu {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  padding: 0.3125rem 0;
  z-index: 999;
  transition: background 0.4s ease 0s, padding 0.2s ease 0s;
  -webkit-transition: background 0.4s ease 0s, padding 0.2s ease 0s;
  background: linear-gradient(to bottom, rgb(45, 10, 18) 0%, rgba(45, 10, 18, 0.4) 100%);
}
@media (min-width: 990px) {
  .top-menu {
    background: linear-gradient(to bottom, rgb(45, 10, 18) 0%, rgba(45, 10, 18, 0) 100%);
    background-size: 100% 130%;
    background-position: bottom center;
    padding: 0.9375rem 0;
  }
}
.top-menu.is-opened {
  background: linear-gradient(to bottom, rgb(45, 10, 18) 0%, rgba(45, 10, 18, 0.4) 100%);
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.13);
}
.top-menu.fixed {
  padding: 0.3125rem 0;
  background-position: top center;
  background-size: 100% 150%;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.13);
}
.modal-open .top-menu {
  width: calc(100vw - 17px);
}
.top-menu .navbar-brand {
  margin: 0;
  padding: 0;
  max-width: 8.4375rem;
}
.top-menu .navbar {
  width: 100%;
  padding: 0;
  margin: 0;
}
@media (min-width: 990px) {
  .top-menu .navbar {
    width: auto;
  }
}
.top-menu .navbar .row {
  width: 100%;
  margin: 0;
}
@media (max-width: 990px) {
  .top-menu .nav-item {
    justify-content: center;
    display: flex;
    flex-direction: column;
  }
  .top-menu .nav-item .nav-link {
    width: auto;
    float: none;
    align-self: center;
  }
}
.top-menu .nav-link {
  text-align: center;
  text-decoration: none !important;
  padding: 1.6875rem 0.625rem !important;
  margin: 0 0.25rem;
  transition: all 0.2s ease-in-out 0s;
  font-size: 1rem;
  color: #ffffff;
}
@media (min-width: 990px) {
  .top-menu .nav-link:hover {
    color: #ffffff;
    text-decoration: underline !important;
  }
}
@media (max-width: 1200px) {
  .top-menu .nav-link {
    padding: 0.625rem 0.4375rem !important;
  }
}

.footer {
  padding: 0;
}
.footer .navbar-brand {
  display: flex;
  width: 100%;
  margin: 0 auto;
  max-width: 9.8125rem;
}
.footer #footerMenu {
  gap: 1.75rem;
  margin: 2.5rem 0;
}
@media (max-width: 990px) {
  .footer #footerMenu {
    gap: 1.25rem;
  }
}
.footer .footer-bottom {
  background: rgba(255, 255, 255, 0.02);
  width: 100%;
  float: left;
}
.footer .footer-bottom p {
  opacity: 0.4;
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  min-width: 0.0625rem;
  transition: all 0.2s ease-in-out 0s;
  border-radius: 0;
  font-family: "Sapiens";
  margin: 0 !important;
}
.btn.btn-default {
  font-size: 1.25rem;
  gap: 0.3125rem;
  color: #101D3F;
  min-height: 4.0625rem;
  padding: 0 1.375rem !important;
  background-image: url(../img/btn-bg.webp);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
@media (min-width: 990px) {
  .btn.btn-default:hover {
    color: #6D3D97;
  }
}
@media (max-width: 576px) {
  .btn.btn-default {
    width: 100%;
  }
}
.btn.btn-play {
  min-width: 3.5625rem;
  width: 3.5625rem;
  padding: 0 !important;
  min-height: 3.5625rem;
  height: 3.5625rem;
  background: none;
  border: none;
  border-radius: 50%;
}
@media (min-width: 990px) {
  .btn.btn-play:hover svg g circle:first-child {
    fill: #8660ce;
  }
}
@media (min-width: 767px) {
  .btn.btn-play {
    width: auto;
    min-width: 8.5625rem;
    min-height: 8.5625rem;
    height: 8.5625rem;
  }
}

#youtubem .modal-content {
  border: none;
  border-radius: 0;
  background: none;
}
#youtubem .modal-content .videoWrapper {
  border: 0.3125rem solid #ffffff;
}
#youtubem .modal-header {
  border: none;
  padding: 0;
}

.videoWrapper {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
}
.videoWrapper iframe,
.videoWrapper video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.btn.btn-x {
  padding: 0 0 1px !important;
  min-height: 1.5625rem;
  width: 1.5625rem;
  color: #ffffff;
  margin-right: 0.625rem !important;
}
@media (min-width: 767px) {
  .btn.btn-x {
    margin-right: -0.9375rem !important;
  }
}
@media (min-width: 990px) {
  .btn.btn-x:hover {
    color: #101D3F;
    background-color: #ffffff;
  }
}

.slick-slider {
  position: relative;
  width: 100%;
  float: left;
}
@media (max-width: 767px) {
  .slick-slider {
    margin-top: 1.25rem;
  }
}

.slick-arrow {
  position: relative;
  width: 4.1875rem;
  height: 3.625rem;
  padding-bottom: 5px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  background: none;
  z-index: 15;
  right: 0;
  left: auto;
  top: -3.125rem;
}
@media (min-width: 767px) {
  .slick-arrow {
    bottom: auto;
    right: -0.75rem;
    left: auto;
    top: -6.75rem;
  }
}
@media (min-width: 990px) {
  .slick-arrow:hover:before {
    background-color: #101D3F;
  }
  .slick-arrow:hover:after {
    top: 0;
    left: 0;
  }
  .slick-arrow:hover svg path {
    fill: #F6E755;
  }
}
@media (min-width: 1200px) {
  .slick-arrow {
    left: calc(100vw - (100vw - 1080px));
  }
}
@media (min-width: 1500px) {
  .slick-arrow {
    left: calc(100vw - (100vw - 1230px));
  }
}
.slick-arrow svg {
  position: relative;
  z-index: 5;
}
.slick-arrow svg path {
  transition: all 0.25s ease-in-out 0s;
}
.slick-arrow:after {
  content: "";
  width: 3.6875rem;
  height: 3.375rem;
  position: absolute;
  left: 4px;
  top: 4px;
  background-color: #101D3F;
  clip-path: polygon(20% 0, 100% 0%, 100% 100%, 0% 100%);
  z-index: -1;
  transition: all 0.25s ease-in-out 0s;
}
.slick-arrow:before {
  content: "";
  width: 3.6875rem;
  height: 3.375rem;
  position: absolute;
  left: 0;
  top: 0;
  background: #F6E755;
  clip-path: polygon(20% 0, 100% 0%, 100% 100%, 0% 100%);
  z-index: 0;
  transition: all 0.25s ease-in-out 0s;
}
.slick-arrow.slick-prev {
  margin-right: 3.4375rem;
  margin-left: 0;
  justify-content: start;
  padding-left: 12px;
}
@media (min-width: 1200px) {
  .slick-arrow.slick-prev {
    margin-left: -3.4375rem;
    margin-right: 0;
  }
}
.slick-arrow.slick-prev:after {
  width: 3.6875rem;
  height: 3.375rem;
  clip-path: polygon(0 0, 100% 0%, 80% 100%, 0% 100%);
}
.slick-arrow.slick-prev:before {
  width: 3.6875rem;
  height: 3.375rem;
  clip-path: polygon(0 0, 100% 0%, 80% 100%, 0% 100%);
}
.slick-arrow.slick-disabled {
  cursor: not-allowed !important;
}

.slick-dots {
  list-style: none;
  margin: 0.9375rem 0 0;
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  padding: 0;
}
.slick-dots li {
  text-align: center;
}
.slick-dots li button {
  border: none;
  background: none;
  padding: 0;
  margin: 0 0.4375rem;
  width: 0.5625rem;
  height: 0.5625rem;
  border-radius: 50%;
  background-color: #101D3F;
  color: transparent;
  font-size: 0.00625rem;
}
.slick-dots li.slick-active button {
  background-color: #EC6122;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1178px;
  }
}
@media (min-width: 1500px) {
  .container {
    max-width: 1320px;
  }
}
.page-content {
  position: relative;
  background-color: #040813;
  z-index: 30;
  padding-top: 3.125rem;
  padding-bottom: 15.625rem;
}
@media (max-width: 990px) {
  .page-content {
    padding-bottom: 9.375rem;
  }
}
.page-content:before {
  content: "";
  aspect-ratio: 1440/686;
  background-image: url(../img/violet.webp);
  background-size: 100% auto;
  background-repeat: no-repeat;
  background-position: top center;
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  transform: translateY(-11%);
}
@media (max-width: 990px) {
  .page-content:before {
    background-size: auto 100%;
  }
}

#nonParallaxContent {
  display: block;
}
@media (min-width: 767px) {
  #nonParallaxContent {
    display: none;
  }
}
#nonParallaxContent .section-bg {
  background-size: cover;
}

.section-mobile-hero {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

#parallaxContent {
  display: none;
  aspect-ratio: 2880/2350;
}
@media (min-width: 767px) {
  #parallaxContent {
    display: block;
  }
}

.parallax {
  position: relative;
  aspect-ratio: 2880/2350;
  z-index: 10;
}

@media (max-width: 1200px) and (min-width: 767px) {
  .hero-image > img {
    max-width: 45%;
  }
}
@media (max-height: 800px) and (min-width: 767px) {
  .hero-image > img {
    max-width: 35%;
  }
}
@media (max-width: 767px) {
  .hero-image > img {
    max-width: 80%;
  }
}

.parallax-holder {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  aspect-ratio: 2880/3250;
  transition: all 0s linear 0s;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 100% auto;
  will-change: transform;
}
.parallax-holder:not(.content-holder) img {
  width: 100%;
}
.parallax-holder.content-holder {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  aspect-ratio: 16/9;
  max-height: 100vh;
}
@media (max-width: 990px) {
  .parallax-holder.content-holder {
    min-height: 60vh;
  }
}
.parallax-holder.bg-1 {
  background-image: url(../img/bg_1_2.webp);
  background-position: top center;
  background-attachment: fixed;
}
.parallax-holder.bg-2 {
  background-image: url(../img/rider_top_2.webp);
}
.parallax-holder.bg-3 {
  background-image: url(../img/rider_left_2.webp);
}
.parallax-holder.bg-4 {
  background-image: url(../img/rider_right_2.webp);
}
.parallax-holder.bg-5 {
  background-image: url(../img/bg_2_2.webp);
}
.parallax-holder.bg-6 {
  background-image: url(../img/bg_3_2.webp);
}
.parallax-holder.bg-7 {
  background-image: url(../img/bg_4_2.webp);
}
.parallax-holder.bg-8 {
  background-image: url(../img/bg_5_2.webp);
}
.parallax-holder.bg-9 {
  background-image: url(../img/bg_6_2.webp);
}
.parallax-holder.holder-1 {
  z-index: 1;
}
.parallax-holder.holder-2 {
  z-index: 2;
}
.parallax-holder.holder-3 {
  z-index: 3;
}
.parallax-holder.holder-4 {
  z-index: 4;
}
.parallax-holder.holder-5 {
  z-index: 5;
}
.parallax-holder.holder-6 {
  z-index: 6;
}
.parallax-holder.holder-7 {
  z-index: 7;
}
.parallax-holder.holder-8 {
  z-index: 8;
}
.parallax-holder.d-flex {
  min-height: 100%;
}
.parallax-holder .button-holder {
  margin-top: -1.875rem;
}

.mt-60 {
  margin-top: 3.75rem;
}

.pt-180 {
  padding-top: 11.25rem;
}
@media (max-width: 990px) {
  .pt-180 {
    padding-top: 3.125rem;
  }
}

.socials-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  gap: 2.1875rem;
  margin-top: 3.125rem;
}
@media (max-width: 990px) {
  .socials-nav {
    gap: 0.75rem;
  }
}
.socials-nav .nav-link {
  position: relative;
  transition: all 0.25s ease-in-out 0s;
}
.socials-nav .nav-link svg path {
  transition: all 0.25s ease-in-out 0s;
}
@media (min-width: 990px) {
  .socials-nav .nav-link:hover .link-inner:after {
    opacity: 1;
  }
  .socials-nav .nav-link:hover svg path {
    fill: #101D3F;
  }
}
.socials-nav .link-inner {
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.socials-nav .link-inner:after {
  position: absolute;
  content: url(../img/shape-social.png);
  top: 0rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
  opacity: 0;
  transition: all 0.25s ease-in-out 0s;
}

.shape-section {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 8.0625rem;
  left: 0;
  z-index: 1;
  background-position: top 6.3125rem center;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.shape-section-right {
  position: absolute;
  width: 33.75rem;
  height: 49.375rem;
  top: 177px;
  right: 0;
  z-index: 8;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.shape-section-left {
  position: absolute;
  width: 49.375rem;
  height: 50rem;
  bottom: 0;
  left: 0;
  z-index: 9;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: 100% auto;
}

.grid-holder img {
  display: block;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .grid-holder img {
    margin: 0 auto 1.875rem;
  }
}
.grid-holder h2 {
  filter: drop-shadow(0px 4px 0px #640E67);
}

.video-holder {
  position: relative;
  width: 100%;
  aspect-ratio: 830/482;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  margin: 0.625rem auto 0;
}
@media (min-width: 990px) {
  .video-holder {
    min-height: 30.125rem;
    max-width: 51.875rem;
    padding-bottom: 0;
  }
}
.video-holder .btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 8.5625rem;
}
